.add-cell {
  position: relative;
  opacity: 0;
  transition: opacity 0.3s ease-in 0.1s;
  margin: 10px 0;
}
.add-cell:active {
  opacity: 0 !important;
  transition: opacity 0s;
}
.add-cell:hover {
 opacity: 1.0;
}
.add-cell.force-visible {
  opacity: 1.0;
}
.add-cell .divider {
 position: absolute;
 top: 50%;
 bottom: 50%;
 right: 2.5%;
 left: 2.5%;
 border-bottom: 1px solid grey;
 width: 90%;
 z-index: -1; 
}
.add-buttons {
  display: flex;
  justify-content: center;
}
.add-buttons .button .icon.is-small {
  height: 2em;
  width: 3em;
}
.add-cell .add-buttons button {
  margin: 0 20px;
}